#navbar {
  z-index: 9999;
  background-color: #fff;
  justify-content: center;
  width: 100%;
  height: 44px;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
}

#navbar a {
  color: #000000b8;
}

#navbar .navbar-link-pop {
  font-size: 24px;
  font-weight: 600;
}

#navbar .navbar-icon-button {
  background: none;
  border: none;
  padding-left: 0;
  padding-right: 0;
}

#navbar .container {
  box-sizing: border-box;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  max-width: 1024px;
  padding: 0 22px;
  display: flex;
}

#navbar .container #menu-trigger {
  display: none;
}

#navbar .container #menu-close-button {
  background: none;
  border: none;
  width: 24px;
  height: 24px;
  display: none;
}

#navbar .container .nav-menu-container {
  justify-content: space-between;
  width: 86%;
  display: flex;
}

#navbar .container .dropdown {
  z-index: 10000;
}

#navbar .container .dropdown .dropdown-content {
  backdrop-filter: blur(10px);
  justify-content: center;
  width: 100%;
  height: 0;
  transition: height .3s;
  position: fixed;
  top: 44px;
  left: 0;
  overflow: hidden;
}

#navbar .container .dropdown .dropdown-content.displayed {
  height: 100%;
}

#navbar .container .dropdown .dropdown-content .wrapper {
  background-color: #fff;
  justify-content: center;
  width: 100%;
  display: flex;
}

#navbar .container .dropdown .dropdown-content .sub-links {
  box-sizing: border-box;
  flex-direction: row;
  gap: 100px;
  width: 1024px;
  padding: 20px 22px;
  display: flex;
}

#navbar .container .dropdown .dropdown-content .sub-links .title {
  padding-bottom: 20px;
}

#navbar .container .dropdown .dropdown-content .sub-links li {
  padding-top: 10px;
  padding-bottom: 10px;
}

@media screen and (width <= 833px) {
  #navbar .container #brand {
    margin-right: auto;
  }

  #navbar .container #search, #navbar .container #shop {
    padding-left: 10px;
    padding-right: 10px;
  }

  #navbar .container #menu-trigger {
    padding-left: 10px;
  }

  #navbar .container .nav-menu-container {
    box-sizing: border-box;
    background-color: #fff;
    gap: 20px;
    width: 100%;
    height: 0;
    max-height: calc(100vh - 44px);
    padding: 0 22px;
    display: grid;
    position: absolute;
    top: 44px;
    left: 0;
    overflow: scroll;
  }

  #navbar .container .nav-menu-container.displayed {
    height: auto;
    padding-bottom: 22px;
  }

  #navbar .container .nav-menu-container .dropdown-content {
    position: relative;
    top: 0;
  }

  #navbar .container .nav-menu-container .dropdown-content .sub-links {
    flex-direction: column;
    gap: 20px;
  }

  #navbar .container #menu-trigger, #navbar .container #menu-close-button {
    display: block;
  }

  #navbar #brand {
    margin-right: auto;
  }
}
