@import "variable";

#navbar {
  height: 44px;
  width: 100%;
  display: flex;
  justify-content: center;
  background-color: $color-white;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 9999;

  a {
    color: $color-link;
  }

  .navbar-link-pop {
    font-size: 24px;
    font-weight: 600;
  }

  .navbar-icon-button {
    border: none;
    background: none;
    padding-right: 0px;
    padding-left: 0px;
  }
  .container {
    display: flex;
    width: 100%;
    justify-content: space-between;
    align-items: center;
    max-width: 1024px;
    padding: 0 22px;
    box-sizing: border-box;

    #menu-trigger {
      display: none;
    }

    #menu-close-button {
      display: none;
      width: 24px;
      height: 24px; 
      background :none; 
      border: none;
    }

    .nav-menu-container {
      display: flex;
      justify-content: space-between;
      width: 86%;
    }

    .dropdown {
      z-index: 10000;
      .dropdown-content {
        height: 0;
        overflow: hidden;
        position: fixed;
        justify-content: center;
        top: 44px;
        left: 0;
        width: 100%;
        backdrop-filter: blur(10px);
        transition: height 0.3s;

        &.displayed {
          height: 100%;
        }
        .wrapper {
          width: 100%;
          background-color: $color-white;
          display: flex;
          justify-content: center;
        }
        .sub-links {
          display: flex;
          flex-direction: row;
          width: 1024px;
          padding-left: 22px;
          padding-right: 22px;
          box-sizing: border-box;
          gap: 100px;
          padding-top: 20px;
          padding-bottom: 20px;

          .title {
            padding-bottom: 20px;
          }
          li {
            padding-top: 10px;
            padding-bottom: 10px;
          }
        }
      }
    }
  }
}

// navbar breakpoint
@media screen and (max-width: 833px) {
  #navbar .container {
    #brand {
      margin-right: auto;
    }
    #search {
      padding-left: 10px;
      padding-right: 10px;
    }
    #shop {
      padding-left: 10px;
      padding-right: 10px;
    }
    #menu-trigger {
      padding-left: 10px;
    }
    .nav-menu-container {
      height: 0;
      display: grid;
      position: absolute;
      top: 44px;
      left: 0;
      background-color: $color-white;
      width: 100%;
      padding: 0 22px;
      gap: 20px;
      max-height: calc(100vh - 44px);
      overflow: scroll;
      box-sizing: border-box;
      &.displayed {
        height: auto;
        padding-bottom: 22px;
      }
      .dropdown-content {
        position: relative;
        top: 0;
        .sub-links {
          flex-direction: column;
          gap: 20px;
        }
      }
    }
    #menu-trigger {
      display: block;
    }
    #menu-close-button {
      display: block;
    }
  }
  #navbar #brand {
    margin-right: auto;
  }
}
